.header {
    background-color: #4C489D;
    color: white;
    display: flex;
    justify-content: center;
    position: relative;

}

.headerContainer {
    width: 100%;
    max-width: 1024px;
    margin: 20px 0px 100px 0px;
}

.headerContainer.listMode {
    margin: 20px 0px 50px 0px;
}

.headerList {
    display: flex;
    gap: 50px;
}

.headerTitle {
    font-family: Raleway, sans-serif;
}

.headerListItems {
    display: flex;
    align-items: center;
    gap: 10px;
    font-family: Raleway, sans-serif;
}

.headerListItems.active {
    border: 2px solid white;
    padding: 5px;
    border-radius: 20px;
}

/* .active{
    border: 2px solid white;
    
    border-radius: 20px;
} */


.headerDesc {
    margin: 20px 0px;
    font-family: Raleway, sans-serif;
}

.headerButton {
    background-color: #433e8a;
    color: white;
    font-weight: 500;
    border: none;
    padding: 5px;
    cursor: pointer;
    border-radius: 5px;
}

.headerLink {
    text-decoration: none;
}

.headerSearch {
    height: 30px;
    background-color: white;
    border: 3px solid #febb02;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 20px 10px;
    border-radius: 5px;
    position: absolute;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;
    z-index: 2;
}

.iconSearch {
    color: black;
}

.headerSearchItem {
    display: flex;
    align-items: center;
    gap: 10px;
}

.headerSearchInput {
    border: none;
    outline: none;
}

.headerSearchText {
    color: lightgrey;
    cursor: pointer;
}