.spinner-parent{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.534);
    z-index: 100;
}

.spinner-border{
    color: white;
    height: 60px;
    width: 60px;
}