
.navbar{
    height: 50px;
    background-color: #4C489D;
    display: flex;
    justify-content: center;
}

.navContainer{
    width: 100%;
    max-width: 1024px;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: bold;
    font-size: 25px;
}

.navButton{
    margin-left: 20px;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    color: #4C489D;
    border-radius: 5px;
}

.dropLink{
    text-decoration: none;
}