.special{
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.specialItem{
    position: relative;
    color: black;
    border-radius: 10px;
    overflow: hidden;
    height: 250px;
}

.specialImg{
    width: 100%;
    object-fit: cover;
}

.specialTitles{
    position: absolute;
    bottom: 20px;
    left: 20px;
}
.popHead{
    font-family: Raleway, sans-serif;
}