.bus-container{
    width: 300px;
    border: 2px solid gray;
    padding: 10px;
}

.seat{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border: 1px solid gray;
    border-radius: 2px;
}

.selected-seat{
    background-color: green;
    color: white;
}

.booked-seat{
    background-color: #4C489D;
    color: white;
    pointer-events: none;
    cursor: disabled;
}

.disabled-button{
    background-color: gray;
    color: white;
    padding: 10px 20px;
    cursor: not-allowed;
}